@use 'base/mixins' as m;
@use 'base/variables' as v;

.boleto{
    padding: 5rem 0;
    h3{
        text-align: center;
        color:v.$blanco;
        margin-bottom: 2rem
    }
    .contenido-boleto{
        @include m.tablet{
            @include m.grid(2,5rem);
        }
    }
}
.pase{
    color: v.$negro;

    .nombre-pase{
        text-align: center;
        color:v.$blanco;
        font-size: 2.8rem;
    }
    &-header,
    &-footer{
        padding: .5rem;
        p{
            text-align: center;
            color: v.$blanco;
            margin: 0;
        }
    }
    &-header{
       background-color: v.$rosa;
       p{
        text-transform: uppercase;
        font-size: 1.6rem;
       }
    }
    &-body{
        background-color: v.$blanco;
        ul{
            @include m.resetearLista;
            li,:nth-child(odd){
                background-color: v.$blanco;
                border-bottom: 1px solid #e1e1e1;
                padding: 1rem;
            }
        }
    }
    &-footer{
       background-color: v.$morado;
       p{
        font-size: 2.4rem;
        font-weight:700;
       }
    }
}