@use 'base/mixins' as m;
@use 'base/variables' as v;

.lineup {
    background-color: v.$rosa;
    padding: 5rem 0;

    h3,
    p {
        text-align: center;
        color: v.$blanco;
    }

    .dia {
        text-transform: uppercase;
        margin: 5rem 0;
    }

    .escenario {
        padding-top: 2rem;
    }

    .nombre-escenario {
        margin-top: 2rem;

        &:after {
            display: block;
            content: '';
            width: 20rem;
            height: 10rem;
            background-size: 15rem;
            background-repeat: no-repeat;
            background-position: center center;
            margin: 2rem auto 0 auto;
        }
    }
}

.edm .nombre-escenario:after {
    background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
    background-image: url(../img/icono_rock.png);
}

.escenario-contenedor {
    @include m.tablet {
        @include m.grid(2, 2rem);
    }

    p {
        color: v.$negro;
    }
}

.bg-amarillo {
    background-color: v.$amarillo;
    li:nth-child(odd){
        background-color: darken(v.$amarillo,15%);
    }
}

.bg-verde {
    background-color: v.$verde;
    li:nth-child(odd){
        background-color: darken(v.$verde,10%);
    }
}

.calendario {
    @include m.resetearLista;

    li {
        padding: 2.5rem;
        font-weight: 700;
        span{
            font-weight: 400;
        }
    }
}